<template>
  <div class="currency-code">
    <div class="topTitle">
      <span class="topTitle-span1">系统管理</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">基础设置</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">货币代码</span>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>查询条件
        </div>
        <div class="contentCellBody">
          <Form
            ref="formValidate"
            :model="formValidate"
            :rules="ruleValidate"
            :label-width="120"
            inline
          >
            <Row>
              <FormItem label="英文代码：" prop="enCode">
                <Input v-model="formValidate.enCode" placeholder="请输入英文代码" style="width:200px"></Input>
              </FormItem>
              <FormItem label="中文名称：" prop="chName">
                <Input v-model="formValidate.chName" placeholder="请输入中文名称" style="width:200px"></Input>
              </FormItem>
            </Row>
            <Row>
              <div class="btnClass">
                <Button
                  type="primary"
                  @click="handleSubmit(0, 1)"
                >查询</Button>
              </div>
            </Row>
          </Form>
        </div>
      </div>
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>数据列表
        </div>
        <div class="contentCellBody">
          <Row>
            <table class="tableBox">
              <thead>
                <tr>
                  <td v-for="(item,index) in tableHeader">{{item.name}}</td>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item,index) in tableBody">
                  <td>{{pageSize*(pageNum-1)+index+1}}</td>
                  <td>{{item.enCode}}</td>
                  <td>{{item.nmCode}}</td>
                  <td>{{item. chName}}</td>
                </tr>
              </tbody>
            </table>
          </Row>
        </div>
      </div>
      <div class="flexCenter pageBox">
        <Page :current.sync="pageNum" :total="rowCount" :page-size="pageSize" @on-change="pageChange" show-elevator />
      </div>
    </div>
  </div>
</template>
<script>
export default {
  created() {
    this.handleSubmit(0,1);
  },
  data() {
    return {
      pageSize:10,
      pageNum:1,
      rowCount:0,
      currentpage:1,
      tableHeader: [
        { name: "序" },
        { name: "英文代码" },
        { name: "数字代码" },
        { name: "中文名称" },
      ],
      tableBody: [
      ],
      formValidate: {
        enCode: "", //英文代码
        chName: "" //中文名称
      },
      ruleValidate: {
      }
    };
  },
  methods: {
    /**
     * name:查询方法
     * ifExacte:	 0 普通 1 精确
     * pageNo: 页数(1)
     * * */
    handleSubmit(ifExacte, pageNo){
      let data = {
        enCode: this.formValidate.enCode.trim(),//地区代码
        chName: this.formValidate.chName.trim() ,//名字
        ifExacte:ifExacte,
      }
      this.axios
      .post(`/system/money/listSearch/${pageNo}/${this.pageSize}`, data, {
          headers: { "Content-Type": "application/json" }
        })
      .then(response => {
        if (response.code == "200") {
          this.tableBody = response.data.records;
          this.rowCount = response.data.total;
        }
      })
      .catch(error => {});
    },
    pageSizefun(pageSize){
      this.pageSize = pageSize * 1;
      this.handleSubmit(0, 1);
    },
    pageChange(pageNo){
      this.pageNum = pageNo;
      this.handleSubmit(0, pageNo)
    },
  }
};
</script>
<style lang="scss" @scoped>
.currency-code {
  .rightTopbtn {
    margin: 15px 4px;
    float: right;
  }
}
</style>



